*,
*::before,
*::after {
  box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-family: sans-serif;
}

img {
  max-width: 100%;
  display: block;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

button,
input,
textarea,
select {
  font: inherit;
  margin: 0;
  border: none;
  background: transparent;
}

@font-face {
  font-family: DM-Sans;
  src: url("../../assets/fonts/DM_Sans/static/DMSans-Light.ttf");
  font-weight: 300;
}
@font-face {
  font-family: DM-Sans;
  src: url("../../assets/fonts/DM_Sans/static/DMSans-Medium.ttf");
  font-weight: 400;
}
@font-face {
  font-family: DM-Sans;
  src: url("../../assets/fonts/DM_Sans/static/DMSans-SemiBold.ttf");
  font-weight: 600;
}
@font-face {
  font-family: DM-Sans;
  src: url("../../assets/fonts/DM_Sans/static/DMSans-SemiBoldItalic.ttf");
  font-weight: 600;
  font-style: italic;
}
@font-face {
  font-family: DM-Sans;
  src: url("../../assets/fonts/DM_Sans/static/DMSans-Bold.ttf");
  font-weight: 700;
}
@font-face {
  font-family: Bricolage Grotesque;
  src: url("../../assets/fonts/Bricolage_Grotesque/static/BricolageGrotesque-Bold.ttf");
  font-weight: 700;
}